header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding:5px;
}

.note-list {
  list-style-type: none;
  text-align: left;
  margin: 0;
  padding:0;
  display: flex;
  flex-wrap: wrap;
}

.note {
  border: 1px solid #333;
  padding: 10px;
  width: 500px;
  margin: 0 10px 10px 0;
}